import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import PropTypes from 'prop-types';
import useMediaQuery from '@mui/material/useMediaQuery';

export default function LoginButton({ authURL }) {
  const belowmd = useMediaQuery((theme) => theme.breakpoints.down('md'));
  return belowmd ? (
    <Button
      type="submit"
      variant="contained"
      sx={{
        position: 'absolute',
        bottom: 30,
        left: 20,
        borderRadius: '5px',
        backgroundColor: 'white',
      }}
    >
      <a
        href={`https://${authURL}/oauth2/authorize`}
        target="_self"
        style={{ textDecoration: 'none', color: 'white' }}
      >
        <Typography variant="h4" color="primary">Login</Typography>
      </a>
    </Button>
  ) : (
    <Button
      type="submit"
      variant="contained"
      sx={{
        position: 'absolute',
        top: 8,
        right: 170,
        borderRadius: '25px',
        backgroundColor: 'white',
      }}
    >
      <a
        href={`https://${authURL}/oauth2/authorize`}
        target="_self"
        style={{ textDecoration: 'none', color: 'white' }}
      >
        <Typography variant="h6" color="primary">Login</Typography>
      </a>
    </Button>

  );
}

LoginButton.propTypes = {
  authURL: PropTypes.string.isRequired,
};
