import PropTypes from 'prop-types';
//  import PhotoAlbum from 'react-photo-album';
import { useState, useEffect } from 'react';
import { EditorState } from 'draft-js';
import Button from '@mui/material/Button';
import DoneIcon from '@mui/icons-material/Done';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import MemoryCarousel from './MemoryCarousel';
import MemoryContentStructure from './MemoryContentStructure';
import MemoryTitle from './MemoryTitle';
import MemoryDate from './MemoryDate';
import MemoryStory from './MemoryStory';
import MemoryImages from './MemoryImages';
import api from '../Api';
import SimpleSnackbar from '../../components/simple_snackbar';
import './memory.css';

export default function MemoryContent({
  memTitle,
  memDate,
  memEditorState,
  memImages,
  bucket,
  memoryID,
  authURL,
}) {
  const [open, setOpen] = useState(false);
  const [edit, setEdit] = useState(false);
  const [activeIndex, setActiveIndex] = useState(0);
  const [title, setTitle] = useState(memTitle);
  const [date, setDate] = useState(memDate);
  const [editorState, setEditorState] = useState(memEditorState);
  const [images, setImages] = useState(memImages);
  const [snack, setSnack] = useState(null);
  const [snackMessage, setSnackMessage] = useState('');

  console.log(images);
  const maxSteps = images.length;

  useEffect(() => setTitle(memTitle), [memTitle]);
  useEffect(() => setDate(memDate), [memDate]);
  useEffect(() => setEditorState(memEditorState), [memEditorState]);
  useEffect(() => setImages(memImages), [memImages]);

  const handleSnackMessage = (msg) => setSnackMessage(msg);
  const handleSnackBarClose = () => setSnack(false);

  const UpdateDetails = async (item, value) => {
    try {
      const resp = await api.updateMemoryDetails(
        bucket,
        authURL,
        memoryID,
        item,
        value,
      );
      console.log('Response', resp);
      if (resp) {
        handleSnackMessage(`Memory ${item} updated successfully`);
      } else {
        handleSnackMessage(`Error while updating memory ${item}`);
      }
      setSnack(true);
      return resp;
    } catch (error) {
      console.log(error);
      return false;
    }
  };

  const UpdateThumbnail = async (image) => {
    try {
      console.log('thumbnail image in update thumbnail', image);
      const resp = await api.updateMemoryImage(
        bucket,
        authURL,
        memoryID,
        'thumbnail',
        image,
      );
      console.log('Response', resp);
      if (resp) {
        handleSnackMessage('Memory images updated successfully');
      } else {
        handleSnackMessage('Error while updating memory images');
      }
      setSnack(true);
      return resp;
    } catch (error) {
      console.log(error);
      return false;
    }
  };

  const updateTitle = (value) => {
    const resp = UpdateDetails('title', value);
    if (resp.ok) {
      setTitle(value);
    }
  };

  const updateDate = (value) => {
    const resp = UpdateDetails('date', value);
    if (resp.ok) {
      setDate(value);
    }
  };

  const updateEditorState = (value) => {
    const resp = UpdateDetails('story', value);
    if (resp.ok) {
      setEditorState(value);
    }
  };

  const updateImages = (value) => {
    const resp = UpdateDetails('images', value);
    if (resp.ok) {
      setEditorState(value);
    }
  };

  console.log(updateImages);

  const handleClose = () => setOpen(false);

  const handleBack = () => {
    setActiveIndex(
      (prevActiveStep) => (prevActiveStep - 1 + maxSteps) % maxSteps,
    );
  };

  const handleNext = () => {
    setActiveIndex((prevActiveStep) => (prevActiveStep + 1) % maxSteps);
  };

  const handlePhotoClick = (photo, event, index) => {
    setOpen(true);
    setActiveIndex(index);
  };

  const handleEditClick = () => setEdit(true);

  const memoryTitle = (
    <MemoryTitle
      editable={edit}
      text={title}
      handleTitleUpdate={updateTitle}
    />
  );

  const memoryDate = (
    <MemoryDate editable={edit} prevDate={date} handleDateUpdate={updateDate} />
  );

  const storyLayout = (
    <MemoryStory
      editable={edit}
      prevEditorState={editorState}
      handleEditorStateUpdate={updateEditorState}
    />
  );

  const photoLayout = (
    <MemoryImages
      editable={edit}
      images={images}
      handlePhotoClick={handlePhotoClick}
      handleThumbnailUpdate={UpdateThumbnail}
    />
  );

  const updateDone = (
    <Button
      variant="contained"
      component="label"
      onClick={() => window.location.reload(false)}
      sx={{
        color: 'primary.main',
        borderRadius: 5,
        backgroundColor: 'button.main',
        padding: '6px 8px',
      }}
    >
      <DoneIcon size="medium" sx={{ paddingRight: 1 }} />
      <Typography variant="body1" color="muted.main">Edit</Typography>
    </Button>
  );
  console.log('Memory Content', edit, memoryTitle);
  return (
    <>
      {edit ? (
        <Stack
          direction="row"
          justifyContent="flex-start"
          sx={{ backgroundColor: 'button.main', paddingX: 2, paddingY: 3 }}
        >
          {updateDone}
        </Stack>
      ) : null}
      <Box
        component="div"
        sx={{
          backgroundColor: 'button.main',
          margin: edit ? '5px' : 0,
        }}
      >
        <MemoryContentStructure
          header={memoryTitle}
          subheader={memoryDate}
          photoLayout={photoLayout}
          storyLayout={storyLayout}
          memoryID={memoryID}
          bucket={bucket}
          authURL={authURL}
          handleEditClick={handleEditClick}
        />
      </Box>
      <MemoryCarousel
        open={open}
        title={title}
        date={date}
        images={images}
        index={activeIndex}
        handleNext={handleNext}
        handleBack={handleBack}
        handleClose={handleClose}
      />
      <SimpleSnackbar
        snackBarOpen={snack}
        message={snackMessage}
        onClose={handleSnackBarClose}
      />
    </>
  );
}

MemoryContent.defaultProps = {
  memTitle: 'Memory Title',
  memDate: 'Memory Date',
  memEditorState: EditorState.createWithText('Memory Text'),
};

MemoryContent.propTypes = {
  memTitle: PropTypes.string,
  memDate: PropTypes.string,
  memEditorState: PropTypes.instanceOf(EditorState),
  memoryID: PropTypes.string.isRequired,
  bucket: PropTypes.string.isRequired,
  authURL: PropTypes.string.isRequired,
  memImages: PropTypes.arrayOf(
    PropTypes.shape({
      src: PropTypes.string,
      width: PropTypes.number,
      height: PropTypes.number,
    }),
  ).isRequired,
};
